.header {
    position: relative;
    background-color: #FFB8CA;

    &__wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__logo {
        display: block;
        width: 199px;
        height: 50px;
        margin-top: 8px;
        margin-bottom: 25px;
    }

    &__button {
        display: flex;
        border: none;
        background-color: transparent;
        padding-top: 27px;
        z-index: 4;

        @media screen and (min-width: 1200px) {
            display: none;
        }
    }

    &__icon--close {
        display: none;
    }
    &__button-menu {
        display: flex;
        position: relative;
        background-color: #FFFFFF;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.3;
        align-items: center;
        justify-content: center;
        color: #D41443;
        width: 209px;
        height: 44px;
        border: none;
        border-radius: 22px;
        margin-bottom: 19px;
        margin-left: 20px;

        &::after {
            content: "";
            display: inline-block;
            background-image: url(/src/images/vector-header.svg);
            width: 8px;
            height: 10px;
            margin-left: 28px;
        }
    }
}
.header__button.is-open .header__icon--close {
    display: block;
}
.header__button.is-open .header__icon--menu {
    display: none;
}

.menu-container {

    @media screen and (max-width: 1200px) {
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        width: 250px;
        background-color: #E17992;
        padding-top: 60px;
        height: 502px;
        z-index: 3;
        

        &.is-open {
            display: flex;
            flex-direction: column;
            flex-direction: column-reverse;
            justify-content: flex-end;
            height: 100vh;
        }
    }
    &__nav {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.28;
    }
    @media screen and (min-width: 1200px) {
        display: none;
        align-items: center;
        width: 100%;
    }

}
.site-nav {
    &__item {
        border: 1px solid #E18298;
        padding-left: 20px;
        padding-top: 13px;
        padding-bottom: 11px;
    }
    &__link {
        text-decoration: none;
        color: #FFFFFF;

        &--page {
            color: #D41443;
        }
    }

}